.blank-template .orange-bullet{
	display: none;
}

.blank-template .main_container{
	color: #000000;
    margin: 0;
    padding: 0 30px;
}

.blank-template ul{
	list-style-type: none;
}

.terms-popup{
	display: none;
	position: fixed;
    width: 100%;
    height: 100%;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,0.2);
}

.terms-popup iframe{
	width: 600px;
    height: 400px;
    margin: 10% auto 0;
}

.terms-popup i{
	position: absolute;
    top: 15px;
    right: 20px;
    font-size: 30px;
    color: #000;
    cursor: pointer;
}

@media( max-width: 767px ) {

    .terms-popup iframe{
        width: 100%;
        height: 100%;
        margin: 0;
    }

    .blank-template .main_container{
        padding: 0 15px;
    }    

}