.blank-template {
    &.white {
        .container {
            padding: 0;
            .main_container {
                padding: 0;
                .mobile-app-page {
                    .contact-info-block {
                        width: 100%;
                        max-width: 100%;
                        .details {
                            padding-left: 0!important;
                            margin: 10px 0 0!important;
                        }
                        .telefon-section {
                            margin: 0 0 10px;
                        }
                        .locatie-section {
                            p {
                                padding-left: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    &#dark {
        background: #1F2326;
    }
}