@import 'modules/iframe';
@import 'modules/blank-page';

.amount-banner {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	font-size: 16px;
	line-height: 20px;
	text-align: left;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.05);
	border-radius: 10px;
	background: rgba(255, 78, 0, .1);
	padding: 11px 15px;
	margin: 0 0 20px;
}

.amount-banner svg {
	margin-right: 10px;
}

.amount-banner svg path{
	stroke: #FF4E00;
}

.woocommerce-mini-cart__buttons a.button {
	position: relative;
	font-weight: 700;
	line-height: 1;
}

.back-to-shopping {
	margin: 0;
    padding: 5px 0 0;
}

.back-to-shopping a{
	cursor: pointer;
	margin: 0!important;
}

.woocommerce-table .shipped_via {
	display: none!important;
}

.woocommerce-message {
	max-width: 695px;
    margin: 0 auto 30px;
    background: #fff !important;
    -webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, .05);
    box-shadow: 0px 0px 25px rgba(0, 0, 0, .05);
    border-radius: 10px;
    padding: 30px;
    border: none;
}

.woocommerce .woocommerce-message:before {
	display: none;
}

.woocommerce.woocommerce-account .woocommerce-message .custom-message {
	display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    font-size: 16px !important;
    font-weight: 500;
    color: #17191C !important;
    line-height: 24px;
    text-align: left;
    padding: 0;
}

.woocommerce .woocommerce-message .custom-message .error-image {
    max-width: 24px !important;
    margin-right: 5px;
    position: relative;
    top: 0;
}

.lost_reset_password button[type="submit"]:disabled {
	background: #ff4e00;
	color: #fff;
}

.error-login-wrapper .showlogin {
	color: #17191C !important;
	cursor: auto;
}